import {sample} from "effector";
import {$gameReset, $moveMade} from "./events";
import {generateTileAndRemoveAnimationFx, resetGameFx, saveNewBoardFx} from "./effects";
import {$isEndGame} from "./stores";

sample(
    {
        source: $moveMade,
        target: saveNewBoardFx
    }
)

sample(
    {
        source: $gameReset,
        target: resetGameFx
    }
)

sample({
        source: saveNewBoardFx.doneData,
        filter: () => !$isEndGame.getState(),
        target: generateTileAndRemoveAnimationFx
    }
)